@import "../../../assets/styles/vars.scss";

.select {
    position: relative;
    display: inline-block;
    font-size: 1rem;
    color: $lightblack;
    width: 100%;
    &--disabled {
        opacity: 0.5;
        pointer-events: none;
        cursor: default;
    }
    // .select__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0.55rem 0.75rem;
        border: 1px solid #ccc;
        cursor: pointer;
        &--open {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
    // .select__placeholder
    &__placeholder {
        flex-grow: 1;
    }
    // .select__icon
    &__icon {
        width: 10px;
        height: 10px;
        border-top: 5px solid #333;
        border-right: 5px solid transparent;
        border-left: 5px solid transparent;
        transform: translateY(3px);
        &--open {
            transform: translateY(-2px) rotate(180deg);
        }
    }
    // .select__options
    &__options {
        width: 100%;
        height: 0;
        max-height: 170px;
        border: 0 solid $grey;
        border-top: none;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        overflow: auto;
        &--open {
            height: auto;
            border-width: 1px;
        }
        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #ccc;
            border-radius: 5px;
        }
        &::-webkit-scrollbar-track {
            background-color: #fff;
        }
    }
    // .select__option
    &__option {
        padding: 8px 12px;
        cursor: pointer;
        font-size: 0.9rem;
        &:hover {
            background-color: rgba($cyan, 0.15);
        }
        &--selected {
            background-color: rgba($blue, 0.7);
            color: $white;
            &:hover {
                background-color: rgba($blue, 0.7);
                color: $white;
            }
        }
    }
}
